.cont {
  align-items: center;
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow-y: auto;
  padding: 10px 5px;
}

.lightCont {
  background: var(--gri-deschis);
  color: var(--welcome-bg-gradient);
}

.darkCont {
  background: var(--fundal);
  color: white;
}

.violetCont {
  background: var(--secondary-color);
  color: white;
}

.tableZone {
  background: var(--panou);
  border-radius: var(--radius-mic);
  box-shadow: 0 3px 18px #0001;
  display: flex;
  flex-direction: column;
  padding: 14px 9px 8px 9px;
  width: 100%;

  position: relative;
}

.historyFilters {
  display: flex;
  gap: 15px;
  flex-wrap: wrap;
  align-items: center;
  /* justify-content: flex-end; */
  margin-bottom: 7px;
}

.paramSelect,
.paramInput {
  background: var(--panou);
  border: 1px solid #353945;
  border-radius: 5px;
  padding: 5px 8px;
  color: var(--text) !important;
}

.dateWrapper {
  position: relative;
  width: fit-content;
}

.dateFormat {
  color: var(--text) !important;
  background: var(--panou);
  top: 50%;
  transform: translateY(-55%);
  right: 27%;
  pointer-events: none;
  position: absolute;
  z-index: 1;
}

.resetButton {
  background: var(--panou);
  color: #eee;
  border: 1px solid #353945;
  border-radius: 7px;
  padding: 7px 20px;
  cursor: pointer;
  font-weight: 600;
}

.historyTable {
  width: 100%;
  border-collapse: collapse;
}

.tableHeaderCell,
.tableDataCell {
  padding: 10px 5px;
  text-align: left;
  border-bottom: 1px solid #353945;
  font-size: clamp(5px, 1.6vw, 14px); /* min, preferat, max */
}

.tableHeaderCell {
  color: var(--accent);
}

.tableDataCell {
  color: var(--text);
}

.historyTotal {
  text-align: right;
  color: var(--accent);
  font-weight: 600;
  margin-top: 5px;
}

.emptyMsg {
  text-align: center;
  color: #555;
  filter: brightness(1.5);
  font-size: 16px;
  padding: 18px 0;
}

.thViolet {
  color: #5255bc !important;
  filter: brightness(1.5);
}

@media screen and (width >=360px) {
  .cont {
    padding: 20px 10px;
  }
}
